.trainers {
  position: relative;
}

.trainers__wrapper {
  padding-top: 129px;
  padding-bottom: 105px;

  @include vp-1199 {
    padding: 114px 101px;
  }

  @include vp-767 {
    padding: 70px 47px;
  }

  h2 {
    margin-top: 0;
    margin-bottom: 69px;
    font-size: 54px;
    font-family: $ff-alumni;
    letter-spacing: 1.62px;

    @include vp-1199 {
      margin-bottom: 47px;
      font-size: 42px;
      text-align: center;
    }

    @include vp-767 {
      margin-bottom: 52px;
      font-size: 32px;
    }
  }
}

.trainers__swiper {
  width: 1160px;

  @include vp-1199 {
    width: 566px;
    height: 294px;
  }

  @include vp-767 {
    width: 226px;
  }
}

.trainers__info {
  position: relative;
  cursor: pointer;

  h3 {
    position: absolute;
    bottom: 5px;
    left: 30%;
    font-size: 15px;
    font-weight: 700;
    color: $color-default-white;
    line-height: 27px;
    letter-spacing: 0.3px;
  }

  img {
    min-height: 294px;
    object-fit: cover;
    z-index: -1;

    @include vp-767 {
      min-height: 274px;
    }
  }
}

.trainers__info:hover {
  background-color: rgba(16, 38, 84, 0.89);

  h3 {
    left: 22%;
    top: 30px;
    z-index: 10;
  }
}


.trainers__info:hover .trainers__text {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background-color: rgba(16, 38, 84, 0.89);
}

.trainers__text {
  display: none;
  width: 100%;
  height: 100%;
  padding: 77px 47px 58px 56px;
  font-size: 13px;
  font-weight: 400;
  color: $color-default-white;
  background-color: $color-transparent;
  line-height: 21px;

  ul {
    margin: 0;
    margin-top: 34px;
    padding: 0;
  }

  li {
    margin-top: 0;
    margin-bottom: 10px;
  }

  span {
    margin-left: 33px;
    font-size: 16px;
  }
}

.trainers__btn {
  position: absolute;
  top: 340px;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  outline: none;
  cursor: pointer;

  @include vp-1199 {
    top: 302px;
  }

  @include vp-767 {
    top: 263px;
    width: 36px;
    height: 36px;
  }

  &--next {
    right: 2%;

    @include vp-1199 {
      right: 4%;
    }

    @include vp-767 {
      right: 1%;
    }

    svg {
      right: 18px;

      @include vp-767 {
        right: 8px;
      }
    }
  }

  &--prev {
    left: 2%;

    @include vp-1199 {
      left: 4%;
    }

    @include vp-767 {
      left: 0;
    }

    svg {
      left: 15px;

      @include vp-767 {
        left: 8px;
      }
    }
  }

  svg {
    position: absolute;
    top: 10px;
    stroke: $color-blue;

    @include vp-767 {
      top: 8px;
      height: 20px;
    }
  }

  @include hover-focus {
    outline: 1px solid $color-blue;
  }

  @include active-no-focus {
    background-color: $color-blue;

    svg {
      stroke: $color-default-white;
    }
  }

  &:disabled {
    svg {
      opacity: 0.3;
    }
  }
}
