@media (max-width: 767px) {
  .contacts__wrapper ul::after {
    display: none;
  }
}

.contacts__item {
  margin-left: 3px;
}
@media (max-width: 1199px) {
  .contacts__item {
    margin-left: 0;
  }
}
.contacts__item h3 {
  margin: 0;
  margin-bottom: 4px;
  font-size: 15px;
  letter-spacing: -0.2px;
}
@media (max-width: 1199px) {
  .contacts__item h3 {
    margin-bottom: 13px;
    font-size: 17px;
  }
}
.contacts__item p {
  margin: 0;
  font-size: 13px;
}
@media (max-width: 1199px) {
  .contacts__item p {
    font-size: 17px;
  }
}
@media (max-width: 1199px) {
  .contacts__item a {
    font-size: 17px;
  }
}

